// Chakra imports
import {
    Flex,
    Box,
    Grid,
    Text,
    Textarea,
    Button,
    Stack,
    useColorModeValue,
    Icon,
    Menu,
    MenuButton,
    MenuList,
    MenuItem,
    MenuDivider,
} from "@chakra-ui/react";
import { FiFile, FiChevronDown } from "react-icons/fi"; // 
// Custom components
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import React, { useState } from "react";

function AITranslate() {
    const textColor = useColorModeValue("gray.700", "white");
    const borderColor = useColorModeValue("gray.200", "gray.600");

    // State for the input text and language settings
    const [inputText, setInputText] = useState("");
    const [translatedText, setTranslatedText] = useState("");
    const [detectedLanguage, setDetectedLanguage] = useState("");
    const [targetLanguage, setTargetLanguage] = useState("");
    const [copyStatus, setCopyStatus] = useState(""); // 

    // Example function to simulate translation
    const translateText = () => {
        const translation = `${inputText}${detectedLanguage}${targetLanguage}`;
        setTranslatedText(translation);
    };

    // Function to copy the translated text to clipboard
    const handleCopyToClipboard = () => {
        navigator.clipboard.writeText(translatedText).then(() => {
            setCopyStatus(""); // 
            setTimeout(() => setCopyStatus(""), 2000); // 2
        }, (err) => {
            console.error(":", err);
        });
    };

    // 
    const languageOptions = ["", "", "", "", ""];

    return (
        <Flex direction="column" pt={{ base: "120px", md: "75px" }}>
            <Grid templateColumns={{ sm: "1fr", lg: "1fr 1fr" }} gap="24px">
                {/*  */}
                <Card>
                    <CardHeader p="6px 0px 22px 0px">
                        <Text fontSize="xl" color={textColor} fontWeight="bold">
                            AI - 
                        </Text>
                    </CardHeader>
                    <CardBody>
                        {/*  */}
                        <Stack direction="row" spacing={0} mb="4">
                            {["", "", ""].map((option) => (
                                <Button
                                    key={option}
                                    colorScheme={detectedLanguage === option ? "blue" : "gray"}
                                    onClick={() => setDetectedLanguage(option)}
                                    borderRadius={option === "" ? "8px 0 0 8px" : option === "" ? "0 8px 8px 0" : "0"}
                                >
                                    {option}
                                </Button>
                            ))}
                        </Stack>

                        {/*  */}
                        <Textarea
                            placeholder="..."
                            value={inputText}
                            onChange={(e) => setInputText(e.target.value)}
                            borderColor={borderColor}
                            mb="6"
                            minH="300px" // 
                        />

                        {/*  */}
                        <Flex justify="flex-end">
                            <Button colorScheme="blue" onClick={translateText}>
                                
                            </Button>
                        </Flex>
                    </CardBody>
                </Card>

                {/*  */}
                <Card>
                    <CardHeader p="6px 0px 22px 0px">
                        <Flex justify="space-between" alignItems="center">
                            <Text fontSize="xl" color={textColor} fontWeight="bold">
                                AI - 
                            </Text>
                            {/*  */}
                            <Button
                                colorScheme="gray" // 
                                onClick={handleCopyToClipboard}
                                leftIcon={<Icon as={FiFile} />}
                                borderRadius="8px" // 
                            >
                                {copyStatus} {/*  */}
                            </Button>
                        </Flex>
                    </CardHeader>
                    <CardBody>
                        {/*  */}
                        <Stack direction="row" spacing={0} mb="4">
                            {["", ""].map((option) => (
                                <Button
                                    key={option}
                                    colorScheme={targetLanguage === option ? "blue" : "gray"}
                                    onClick={() => setTargetLanguage(option)}
                                    borderRadius={option === "" ? "8px 0 0 8px" : "0"}
                                >
                                    {option}
                                </Button>
                            ))}
                            {/*  */}
                            <Menu>
                                <MenuButton
                                    as={Button}
                                    rightIcon={<Icon as={FiChevronDown} />}
                                    colorScheme="gray"
                                    borderRadius="0 8px 8px 0" // 
                                >
                                    
                                </MenuButton>
                                <MenuList>
                                    {languageOptions.map((language) => (
                                        <MenuItem key={language} onClick={() => setTargetLanguage(language)}>
                                            {language}
                                        </MenuItem>
                                    ))}
                                </MenuList>
                            </Menu>
                        </Stack>

                        {/*  Textarea  */}
                        <Textarea
                            value={translatedText}
                            readOnly
                            borderColor={borderColor}
                            minH="300px" // 
                            overflowY="auto"
                        />
                    </CardBody>
                </Card>
            </Grid>
        </Flex>
    );
}

export default AITranslate;
