import {
    Flex,
    Grid,
    Text,
    Button,
    Stack,
    Icon,
    useColorModeValue,
    Box,
    Tabs,
    TabList,
    Tab,
    TabPanels,
    TabPanel
} from "@chakra-ui/react";
import { FiPlus, FiCopy, FiDownload } from "react-icons/fi"; // 
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import React, { useState } from "react";

function MeetSure() {
    const textColor = useColorModeValue("gray.700", "white");
    const borderColor = useColorModeValue("gray.200", "gray.600");

    // 
    const transcript = [
        { speaker: "A", text: "AIAI ", time: "00:05" },
        { speaker: "B", text: " AI ", time: "00:10" },
        { speaker: "A", text: " AI ", time: "00:20" },
        { speaker: "B", text: "AI ", time: "00:30" },
        { speaker: "A", text: "AI ", time: "00:45" },
        { speaker: "B", text: " AI ", time: "01:00" },
        { speaker: "A", text: "", time: "01:15" },
        { speaker: "B", text: "AI ", time: "01:30" },
        { speaker: "A", text: "AI ", time: "01:45" },
        { speaker: "B", text: " AI ", time: "02:00" },
    ];

    //  - 
    const [copyText, setCopyText] = useState("");

    // 
    const handleCopy = () => {
        const allText = transcript.map(entry => `${entry.speaker}: ${entry.text} (${entry.time})`).join("\n");
        navigator.clipboard.writeText(allText);
        setCopyText(""); // 
        setTimeout(() => setCopyText(""), 2000); // 2 
    };

    // 
    const handleDownload = () => {
        const allText = transcript.map(entry => `${entry.speaker}: ${entry.text} (${entry.time})`).join("\n");
        const blob = new Blob([allText], { type: "text/plain" });
        const link = document.createElement("a");
        link.href = URL.createObjectURL(blob);
        link.download = "transcript.txt";
        link.click();
    };

    return (
        <Flex direction="column" pt={{ base: "120px", md: "75px" }}>
            {/*  Card */}
            <Grid templateColumns="1fr" gap="24px" mb="24px">
                <Card>
                    <CardHeader p="6px 0px 22px 0px">
                        <Flex justify="space-between" alignItems="center">
                            <Text fontSize="xl" color={textColor} fontWeight="bold">
                                
                            </Text>
                            {/* + */}
                            <Button colorScheme="blue" leftIcon={<Icon as={FiPlus} />}>
                                
                            </Button>
                        </Flex>
                    </CardHeader>
                    <CardBody>
                        <Text></Text>
                    </CardBody>
                </Card>
            </Grid>

            {/*  Card */}
            <Grid templateColumns={{ sm: "1fr", lg: "1fr 1fr" }} gap="24px">
                {/*  Card */}
                <Card>
                    <CardHeader p="6px 0px 22px 0px">
                        <Flex justify="space-between" alignItems="center">
                            <Text fontSize="xl" color={textColor} fontWeight="bold">
                                
                            </Text>
                            {/*  */}
                            <Stack direction="row" spacing={4}>
                                <Button
                                    onClick={handleCopy}
                                    leftIcon={<Icon as={FiCopy} />}
                                    backgroundColor="gray.200" // 
                                    color="black" // 
                                    _hover={{ bg: "gray.300" }} // 
                                    variant="solid"
                                >
                                    {copyText}
                                </Button>
                                <Button
                                    onClick={handleDownload}
                                    leftIcon={<Icon as={FiDownload} />}
                                    backgroundColor="gray.200" // 
                                    color="black" // 
                                    _hover={{ bg: "gray.300" }} // 
                                    variant="solid"
                                >
                                    .txt
                                </Button>
                            </Stack>
                        </Flex>
                    </CardHeader>
                    <CardBody>
                        {/*  */}
                        <Box
                            maxH="500px" // 
                            overflowY="auto" // 
                            border="1px solid"
                            borderColor={borderColor}
                            borderRadius="md"
                            p="4"
                            css={{
                                "&::-webkit-scrollbar": {
                                    width: "8px",
                                },
                                "&::-webkit-scrollbar-thumb": {
                                    background: "#888",
                                    borderRadius: "10px",
                                },
                                "&::-webkit-scrollbar-thumb:hover": {
                                    background: "#555",
                                },
                            }}
                        >
                            {transcript.map((entry, index) => (
                                <Flex key={index} mb="4">
                                    {/*  */}
                                    <Box
                                        width="4px"
                                        bg="blue.500"
                                        borderRadius="full"
                                        mr="4"
                                    ></Box>
                                    {/*  */}
                                    <Flex direction="column" width="100%">
                                        <Flex justify="space-between" mb="1">
                                            {/*  */}
                                            <Text fontWeight="bold" color={textColor}>
                                                {entry.speaker}
                                            </Text>
                                            <Text color="gray.500">{entry.time}</Text>
                                        </Flex>
                                        {/*  */}
                                        <Text>{entry.text}</Text>
                                    </Flex>
                                </Flex>
                            ))}
                        </Box>
                    </CardBody>
                </Card>

                {/* AI Card */}
                <Card>
                    <CardHeader p="6px 0px 22px 0px">
                        <Text fontSize="xl" color={textColor} fontWeight="bold">
                            AI
                        </Text>
                    </CardHeader>
                    <CardBody>
                        {/*  Tabs  Tab */}
                        <Tabs variant="enclosed">
                            <TabList>
                                <Tab></Tab>
                                <Tab></Tab>
                                <Tab></Tab>
                            </TabList>
                            <TabPanels>
                                <TabPanel>
                                    <Text>
                                         AI 
                                    </Text>
                                </TabPanel>
                                <TabPanel>
                                    <Text>
                                        - [ ]  AI 
                                        <br />
                                        - [ ] 
                                        <br />
                                        - [ ]  AI 
                                    </Text>
                                </TabPanel>
                                <TabPanel>
                                    <Text>
                                        - 
                                        <br />
                                        - 
                                        <br />
                                        - 
                                        <br />
                                        - 
                                        <br />
                                        - 
                                    </Text>
                                </TabPanel>
                            </TabPanels>
                        </Tabs>
                    </CardBody>
                </Card>
            </Grid>
        </Flex>
    );
}

export default MeetSure;
