// Chakra imports
import {
    Flex,
    Box,
    Grid,
    Text,
    Textarea,
    Button,
    Stack,
    useColorModeValue,
    Icon
} from "@chakra-ui/react";
import { FiFile } from "react-icons/fi"; // 
// Custom components
import Card from "components/Card/Card.js";
import CardBody from "components/Card/CardBody.js";
import CardHeader from "components/Card/CardHeader.js";
import React, { useState } from "react";

function AIWrite() {
    const textColor = useColorModeValue("gray.700", "white");
    const borderColor = useColorModeValue("gray.200", "gray.600");

    // State for the email content and settings
    const [emailContent, setEmailContent] = useState("");
    const [length, setLength] = useState("");
    const [format, setFormat] = useState("");
    const [tone, setTone] = useState("");
    const [language, setLanguage] = useState("");
    const [copyStatus, setCopyStatus] = useState(""); // 

    // Example function to simulate email generation
    const generateEmail = () => {
        let generatedContent = `${format}${tone}${length}\n\n${emailContent}`;
        return language === "" ? generatedContent : "This is a " + format + " with a " + tone + " tone and " + length + " length.\n\n" + emailContent;
    };

    // Function to copy the generated email content to clipboard
    const handleCopyToClipboard = () => {
        const content = generateEmail();
        navigator.clipboard.writeText(content).then(() => {
            setCopyStatus(""); // 
            setTimeout(() => setCopyStatus(""), 2000); // 2
        }, (err) => {
            console.error(":", err);
        });
    };

    return (
        <Flex direction="column" pt={{ base: "120px", md: "75px" }}>
            <Grid templateColumns={{ sm: "1fr", lg: "1fr 1fr" }} gap="24px">
                {/*  */}
                <Card>
                    <CardHeader p="6px 0px 22px 0px">
                        <Text fontSize="xl" color={textColor} fontWeight="bold">
                            
                        </Text>
                    </CardHeader>
                    <CardBody>
                        {/*  Textarea */}
                        <Textarea
                            placeholder="..."
                            value={emailContent}
                            onChange={(e) => setEmailContent(e.target.value)}
                            borderColor={borderColor}
                            mb="6"
                            minH="200px"
                        />

                        {/*  */}
                        <Text fontWeight="bold" mb="2"></Text>
                        <Stack direction="row" spacing={4} mb="4">
                            {["", "", ""].map((option) => (
                                <Button
                                    key={option}
                                    colorScheme={length === option ? "blue" : "gray"}
                                    onClick={() => setLength(option)}
                                >
                                    {option}
                                </Button>
                            ))}
                        </Stack>

                        {/*  */}
                        <Text fontWeight="bold" mb="2"></Text>
                        <Stack direction="row" spacing={4} mb="4">
                            {["", "", "", "", ""].map((option) => (
                                <Button
                                    key={option}
                                    colorScheme={format === option ? "blue" : "gray"}
                                    onClick={() => setFormat(option)}
                                >
                                    {option}
                                </Button>
                            ))}
                        </Stack>

                        {/*  */}
                        <Text fontWeight="bold" mb="2"></Text>
                        <Stack direction="row" spacing={4} mb="4">
                            {["", "", "", ""].map((option) => (
                                <Button
                                    key={option}
                                    colorScheme={tone === option ? "blue" : "gray"}
                                    onClick={() => setTone(option)}
                                >
                                    {option}
                                </Button>
                            ))}
                        </Stack>

                        {/*  */}
                        <Text fontWeight="bold" mb="2"></Text>
                        <Stack direction="row" spacing={4} mb="6">
                            {["", ""].map((option) => (
                                <Button
                                    key={option}
                                    colorScheme={language === option ? "blue" : "gray"}
                                    onClick={() => setLanguage(option)}
                                >
                                    {option}
                                </Button>
                            ))}
                        </Stack>

                        {/*  */}
                        <Flex justify="flex-end">
                            <Button colorScheme="blue" onClick={generateEmail}>
                                
                            </Button>
                        </Flex>
                    </CardBody>
                </Card>

                {/*  */}
                <Card>
                    <CardHeader p="6px 0px 22px 0px" display="flex" justifyContent="space-between" alignItems="center">
                        <Text fontSize="xl" color={textColor} fontWeight="bold">
                            
                        </Text>
                        {/*  */}
                        <Button
                            colorScheme="gray" // 
                            onClick={handleCopyToClipboard}
                            leftIcon={<Icon as={FiFile} />} // 
                        >
                            {copyStatus} {/*  */}
                        </Button>
                    </CardHeader>
                    <CardBody>
                        <Textarea
                            value={generateEmail()} // 
                            readOnly // 
                            borderColor={borderColor} // 
                            minH="500px" // 
                            resize="vertical" // 
                            overflowY="auto" // 
                        />
                    </CardBody>
                </Card>
            </Grid>
        </Flex>
    );
}

export default AIWrite;
